<template>
  <div>
    <!-- Question packs view -->
    <v-card flat v-if="!questionsEditing">
      <v-toolbar flat dark color="primary" app>
        <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
        <v-btn
          small
          @click.stop="newQuestionPackDialog = true"
          color="primary"
          class="lighten-1"
        >
          <v-icon>add</v-icon>
          Добавить сборник вопросов
        </v-btn>
      </v-toolbar>

      <v-data-table
        :headers="questionPackHeaders"
        :items="question_packs"
        rows-per-page-text="Показывать на странице: "
        :rows-per-page-items="[10, 25, 50, { 'text': 'All', 'value': -1 }]"
      >
        <template slot="items" slot-scope="props">
          <td><a @click="questionsEditing = !questionsEditing; getQuestionsForEditor(props.item)">{{ props.item.title }}</a></td>
          <td class="justify-center text-xs-right px-0">
            <v-btn icon class="mx-0" @click.native="questionPackEditDialog = true; questionPackForEdit = props.item; newQuestionPackName = props.item.title">
              <v-icon color="teal">edit</v-icon>
            </v-btn>
            <v-btn icon class="mx-0" @click="deleteQuestionPack(props.item)">
              <v-icon color="pink">delete</v-icon>
            </v-btn>
          </td>
        </template>
        <template slot="no-data"><v-alert :value="true" color="warning" icon="warning">Здесь ничего нет</v-alert></template>
      </v-data-table>

      <v-dialog v-model="newQuestionPackDialog" max-width="500px">
        <v-card>
          <v-card-title><span class="headline">Добавить новый сборник вопросов</span></v-card-title>
          <v-card-text>
            <v-text-field
              label="Название"
              required
              v-model="newQuestionPackName"
            ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click.native="newQuestionPackDialog = false">Отмена</v-btn>
            <v-btn color="blue darken-1" flat @click.native="addQuestionPack(); newQuestionPackName = ''">Сохранить</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="questionPackEditDialog" max-width="500px" lazy>
        <v-card>
          <v-card-title><span class="headline">Переименовать сборник вопросов</span></v-card-title>
          <v-card-text>
            <v-text-field
              label="Название"
              required
              v-model="newQuestionPackName"
            ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click.native="questionPackEditDialog = false">Отмена</v-btn>
            <v-btn color="blue darken-1" flat @click.native="questionPackForEdit.title = newQuestionPackName; saveQuestionPack(questionPackForEdit)">Сохранить</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card>

    <!-- Questions view -->
    <v-card flat v-else>
      <v-toolbar flat dark color="primary" app>
        <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
        <v-btn
          small
          @click.stop="showQuestionEditorModal(null); questionEditorDialog = true"
          color="primary"
          class="lighten-1"
        >
          <v-icon>add</v-icon>
          Добавить вопрос
        </v-btn>
        <v-btn
          small
          @click.stop="questionsEditing = false"
          color="primary"
          class="lighten-1"
        >
          <v-icon>arrow_back</v-icon>
          Назад
        </v-btn>
      </v-toolbar>

      <v-data-table
        :headers="questionEditorFields"
        :items="curentQuestionSet"
        rows-per-page-text="Показывать на странице: "
        :rows-per-page-items="[10, 25, 50, { 'text': 'All', 'value': -1 }]"
      >
        <template slot="items" slot-scope="props">
          <td>
            <a
              v-html="props.item.question"
              @click.stop="showQuestionEditorModal(props.item)"
              class="question"
            ></a>
            <v-chip small disabled class="primary white--text">{{ props.item.type }}</v-chip>
          </td>
          <td class="text-xs-right px-0">
            <v-btn icon class="mx-0" @click="deleteQuestion(props.item)">
              <v-icon color="pink">delete</v-icon>
            </v-btn>
          </td>
        </template>
        <template slot="no-data"><v-alert :value="true" color="warning" icon="warning">Здесь ничего нет</v-alert></template>
      </v-data-table>

      <v-dialog
        v-model="questionEditorDialog"
        fullscreen
        transition="dialog-bottom-transition"
        v-on:close="questionEditorDialog = false"
      >
        <question-editor
          :question_pack_id="questionPackForEdit._id"
          :question="newQuestion"
        ></question-editor>
      </v-dialog>
    </v-card>
  </div>
</template>
<script>
import QuestionEditor from './../components/QuestionEditor'
import { mapGetters } from 'vuex'

const defaultQuestion = {
  question: '',
  type: 'выбор',
  choise_answers: [
    { answer: '', correct: false },
    { answer: '', correct: false }
  ]
}

export default {
  components: {
    'question-editor': QuestionEditor
  },

  data () {
    return {
      newQuestionPackName: '',
      newQuestionPackDialog: false,
      questionPackEditDialog: false,
      questionPackHeaders: [
        { value: 'title', text: 'Название' },
        { value: 'actions', text: 'Действия', align: 'right' }
      ],
      questionsEditing: false,

      needUpdateQuestionCounts: false,

      questionEditorDialog: false,
      questionPackForEdit: {},
      questionEditorFields: [
        { value: 'question', text: 'Вопрос' },
        { value: 'actions', text: 'Действия', align: 'right' }
      ],
      newQuestion: JSON.parse(JSON.stringify(defaultQuestion))
    }
  },

  props: ['fields'],

  computed: {
    curentQuestionSet () {
      return this.questions.filter(question => question.question_pack._id === this.questionPackForEdit._id)
    },
    ...mapGetters({
      'question_packs': 'questionPack/questionPacks',
      'questions': 'questions/questions',
      'questionSetCount': 'questions/questionSetCount'
    })
  },

  mounted () {
    this.$store.dispatch('questionPack/GET_QUESTION_PACKS')
    this.$store.dispatch('questions/GET_ALL_QUESTIONS')
  },

  methods: {
    saveQuestionPack (item) {
      this.$store.dispatch('questionPack/SAVE_QUESTION_PACK', item)
      this.questionPackForEdit = {}
      this.newQuestionPackName = ''
      this.questionPackEditDialog = false
    },

    addQuestionPack () {
      this.$store.dispatch('questionPack/ADD_QUESTION_PACK', this.newQuestionPackName)
      this.newQuestionPackDialog = false
    },

    getQuestionsForEditor (item) {
      if (this.questionPackForEdit !== item || this.needUpdateQuestionCounts) this.questionPackForEdit = item
    },

    showQuestionEditorModal (question) {
      if (!question) this.newQuestion = JSON.parse(JSON.stringify(defaultQuestion))
      else {
        this.newQuestion = JSON.parse(JSON.stringify(question))
        if (this.newQuestion.type === 'выбор') this.newQuestion.choise_answers = this.newQuestion.answers
      }
      this.questionEditorDialog = true
    },

    deleteQuestionPack (item) {
      confirm('Вы уверены что хотите удалить сборник вопросов?') && this.$store.dispatch('questionPack/DELETE_QUESTION_PACK', item)
    },

    deleteQuestion (item) {
      confirm('Вы уверены что хотите удалить вопрос?') && this.$store.dispatch('questions/DELETE_QUESTION', item)
    }
  }
}
</script>
<style>
a.question p{
  display: inline-block;
  margin-bottom: 0;
}
</style>



