<template>
  <v-navigation-drawer
    app
    temporary
    v-model="$store.state.navigationDrawer"
  >
    <v-toolbar flat>
      <v-list>
        <v-list-tile :to="{ name: 'Home' }">
          <v-list-tile-title class="title">TestManager</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-toolbar>
    <v-divider></v-divider>
    <v-list>
      <v-list-tile :to="{ name: 'TestEditor' }">
        <v-list-tile-content>Редактор тестов</v-list-tile-content>
      </v-list-tile>
      <v-list-tile :to="{ name: 'QuestionEditor' }">
        <v-list-tile-content>Редактор вопросов</v-list-tile-content>
      </v-list-tile>
    </v-list>
  </v-navigation-drawer>
  <!-- <b-navbar type="dark" variant="info" toggleable="md">
    <b-navbar-toggle target="nav_dropdown_collapse"></b-navbar-toggle>
    <b-navbar-brand :to="{ name: 'Home' }">TestManager</b-navbar-brand>

    <b-collapse is-nav id="nav_dropdown_collapse">
      <b-navbar-nav class="ml-auto">
        <b-nav-item :to="{ name: 'TestEditor' }">Редактор тестов</b-nav-item>
        <b-nav-item :to="{ name: 'QuestionEditor' }">Редактор вопросов</b-nav-item>
        <b-nav-item v-if="!checkAuthentication()"
                    :to="{ name: 'Authentication' }">Войти</b-nav-item>
        <div v-if="checkAuthentication() && user">
          <b-nav-item-dropdown :text='user.username'
                               boundary='window'>
            <b-dropdown-item @click="submitSignout()">Выйти</b-dropdown-item>
          </b-nav-item-dropdown>
        </div>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar> -->
</template>

<script>
import Authentication from '@/components/pages/Authentication'
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState([ 'user' ])
  },

  mounted () {
    if (this.checkAuthentication() && !this.user) this.$store.dispatch('getLoggedUser')
  },

  methods: {
    submitSignout () { Authentication.signout(this, '/') },

    checkAuthentication () { return Authentication.checkAuthentication() }
  }
}
</script>



