<template>
  <div>
    <!-- Test list view -->
    <v-card flat v-if="testListViewing">
      <v-toolbar flat dark color="primary" app>
        <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
        <v-btn
          small
          color="primary"
          class="lighten-1"
          @click.native="newTestDialog = true"
        >
          <v-icon>add</v-icon>
          Добавить тест
        </v-btn>
      </v-toolbar>

      <v-card-text>
        <v-data-table
          :headers="testTableFields"
          :items="tests"
          rows-per-page-text="Показывать на странице: "
          :rows-per-page-items="[10, 25, 50, { 'text': 'All', 'value': -1 }]"
        >
          <template slot="items" slot-scope="props">
            <td>
              <a @click.stop="testListViewing = !testListViewing; currentTest = JSON.parse(JSON.stringify(props.item))">
                {{ props.item.title }}
              </a>
            </td>
            <td>{{ props.item.author.username }}</td>
            <td>
              <v-icon v-if="props.item.published" color="teal">check_circle</v-icon>
              <v-icon v-else color="pink">remove_circle</v-icon>
            </td>
            <td class="text-xs-right px-0">
              <v-btn icon class="mx-0" @click.native="deleteTest(props.item)">
                <v-icon color="pink">delete</v-icon>
              </v-btn>
            </td>
          </template>
        </v-data-table>
      </v-card-text>

            <!-- Add new test dialog -->
      <v-dialog v-model="newTestDialog" max-width="500px">
        <v-card>
          <v-card-title><span class="headline">Добавить новый тест</span></v-card-title>
          <v-card-text>
            <v-text-field
              label="Название"
              required
              v-model="newTestTitle"
            ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click.native="newTestDialog = false">Отмена</v-btn>
            <v-btn
              color="blue darken-1"
              flat
              @click.native="$store.dispatch('test/ADD_TEST_TITLE_ONLY', newTestTitle); newTestTitle = ''; newTestDialog = false"
            >
              Сохранить
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card>

    <!-- Test composer view -->
    <v-card flat v-else>
      <v-toolbar flat dark color="primary" app>
        <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
        <v-btn
          small
          color="primary"
          class="lighten-1"
          @click="testListViewing = true; currentTest = null"
        >
          <v-icon>arrow_back</v-icon>Назад
        </v-btn>
        <v-btn
          small
          color="success"
          @click="$store.dispatch('test/SAVE_TEST', currentTest)"
        >
          <v-icon>save</v-icon>Сохранить
        </v-btn>
        <v-btn
          small
          :color="currentTest.published ? 'success' : 'warning'"
          @click="currentTest.published = !currentTest.published"
        >
          {{ currentTest.published ? 'Опубликовано' : 'Опубликовать'}}
        </v-btn>
      </v-toolbar>

      <v-card-title>
        <span class="headline" v-if="!testTitleEditing">
          {{ currentTest.title }}
          <a @click="testTitleEditing = true"><v-icon small color="primary">edit</v-icon></a>
        </span>
        <v-layout row v-else>
          <v-text-field
            v-model="currentTest.title"
            label="Название"
          ></v-text-field>
          <v-btn icon class="mx-0" @click="testTitleEditing = false">
            <v-icon color="primary">save</v-icon>
          </v-btn>
        </v-layout>
      </v-card-title>
      <v-card-text>
        <div>
          <v-card v-if="testDescriptionEditing">
            <vue-editor
              v-model="currentTest.description"
              :editorToolbar="customToolbar"
              id="description-editor"
            ></vue-editor>
            <v-btn
              color="primary"
              small
              absolute
              bottom
              right
              fab
              @click="testDescriptionEditing = false"
            >
              <v-icon color="white">done</v-icon>
            </v-btn>
          </v-card>
          <v-card v-else flat>
            <v-btn
              color="primary"
              small
              absolute
              bottom
              right
              fab
              @click="testDescriptionEditing = true"
            >
              <v-icon color="white">edit</v-icon>
            </v-btn>
            <span v-html="currentTest.description"></span>
          </v-card>
        </div>

        <v-layout row wrap class="mt-3" justify-space-between>
          <v-flex xs8 class="mr-4">
            <div
              v-for="question in currentTest.questions"
              :key="question._id"
              class="tquestion"
              @click.stop="$store.commit('test/DELETE_QUESTION', { test: currentTest, question })"
            >
              <div>
                <span v-html="question.question"></span>
                <small class="grey--text">{{ question.question_pack.title }}</small>
              </div>
              <v-chip small disabled class="primary white--text">{{ question.type }}</v-chip>
            </div>
          </v-flex>
          <v-flex xs3>
            <v-select
              label="Добавить вопросы из сборника:"
              :items="question_pack_options"
              v-model="selectedQuestonPack"
            ></v-select>
            <div
              v-for="question in questionsOfSelectedQuestionPack"
              :key="question._id"
              class="tquestion"
              :class="questionAlreadyInTest(question) && 'info white--text'"
              @click.stop="$store.commit('test/ADD_QUESTION', { test: currentTest, question })"
            >
              <span v-html="question.question"></span>
              <v-spacer></v-spacer>
              <v-chip dark small disabled>{{ question.type }}</v-chip>
            </div>
          </v-flex>
        </v-layout>
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      testListViewing: true,
      currentTest: null,
      newTestTitle: '',
      newTestDialog: false,

      testTitleEditing: false,
      testDescriptionEditing: false,

      selectedQuestonPack: null,

      testTableFields: [
        { value: 'title', text: 'Название' },
        { value: 'author.username', text: 'Автор' },
        { value: 'published', text: 'Опубликовано' },
        { value: 'actions', text: 'Действия', align: 'rigth' }
      ],
      customToolbar: [
        ['bold', 'italic', 'underline'],
        [{ 'list': 'ordered' }, { 'list': 'bullet' }],
        ['code-block']
      ]
    }
  },

  computed: {
    question_pack_options () {
      let options = []
      for (let item of this.question_packs) {
        options.push({
          value: item,
          text: item.title
        })
      }
      return options
    },

    questionsOfSelectedQuestionPack () {
      if (this.selectedQuestonPack !== null) return this.questions.filter(question => question.question_pack._id === this.selectedQuestonPack._id)
      else return null
    },

    ...mapGetters({
      'tests': 'test/tests',
      'questions': 'questions/questions',
      'question_packs': 'questionPack/questionPacks'
    })
  },

  mounted () {
    this.$store.dispatch('test/GET_TESTS')
    this.$store.dispatch('questionPack/GET_QUESTION_PACKS')
    this.$store.dispatch('questions/GET_ALL_QUESTIONS')
  },

  methods: {
    questionAlreadyInTest (question) {
      let index = this.currentTest.questions.findIndex((element) => {
        return element._id === question._id
      })
      return index !== -1
    },

    deleteTest (item) {
      confirm('Вы уверены что хотите удалить тест') && this.$store.dispatch('test/DELETE_TEST', item)
    }
  }
}
</script>
<style>
.tquestion {
  display: flex;
  padding: 0.5em;
  border: 1px solid rgba(0,0,0,.125) !important;
  margin-bottom: -1px;
}
.tquestion p {
  margin-bottom: 2px;
}
</style>



