<template>
  <v-card>
    <v-toolbar dark color="primary">
      <v-btn icon @click.native="$parent.$emit('close')" dark>
        <v-icon>close</v-icon>
      </v-btn>
      <v-toolbar-title>Редактирование вопроса</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-select
        label="Тип вопроса"
        v-model="question.type"
        :items="questionTypes"
      ></v-select>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn dark flat @click.native="updateQuestion(); $parent.$emit('close')">Сохранить</v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-card-text>
      <vue-editor
        v-model="question.question"
        :editorToolbar="customToolbar"
      ></vue-editor>
      <span>Ответ(ы):</span>

      <!-- Выбор ответа -->
      <div v-if="question.type == 'выбор'">
        <v-layout
          align-center
          v-for="(answer, index) in question.choise_answers"
          :key="index"
        >
          <v-checkbox v-model="answer.correct" hide-details class="shrink mr-2"></v-checkbox>
          <v-text-field :label="`Ответ ${index + 1}`" v-model="answer.answer"></v-text-field>
          <v-btn
            flat
            icon
            color="error"
            @click="() => {if (index !== -1) question.choise_answers.splice(index, 1)}"
            v-if="index != question.choise_answers.length - 1"
          ><v-icon>remove_circle</v-icon>
          </v-btn>
          <v-btn
            flat
            icon
            color="success"
            @click="question.choise_answers.push({ answer: '', correct: false })"
            v-if="index == question.choise_answers.length - 1"
          ><v-icon>add_circle</v-icon>
          </v-btn>
        </v-layout>
      </div>

      <div v-else-if="question.type == 'ввод ответа'">
        {{ question.type }}
      </div>
    </v-card-text>
  </v-card>
</template>
<script>
export default {
  data () {
    return {
      questionTypes: [ 'выбор', 'ввод ответа' ],
      customToolbar: [
        ['bold', 'italic', 'underline'],
        [{ 'list': 'ordered' }, { 'list': 'bullet' }],
        ['code-block']
      ]
    }
  },

  props: [ 'question_pack_id', 'question' ],

  methods: {
    updateQuestion () {
      if (this.question.type === 'выбор') this.question.answers = this.question.choise_answers
      var newQuestion = {
        '_id': this.question._id,
        'question': this.question.question,
        'type': this.question.type,
        'question_pack': this.question_pack_id,
        'answers': this.question.answers
      }

      if (!this.question._id) this.$store.dispatch('questions/ADD_QUESTION', newQuestion)
      else this.$store.dispatch('questions/SAVE_QUESTION', newQuestion)
    }
  }
}
</script>



